<template>
  <div class="text-white text-left" v-if="type == 'origin'" style="max-width: 300px">
    <div class>
      <img :src="getIconURL(name)" :alt="name" width="30px" />
      <h6 class="mx-2 d-inline">{{name}}</h6>
    </div>
    <div class="">{{ originObject[name].effect }}</div>
    <div class="border-dark border-top border-bottom px-2" v-for="bonus in originObject[name].bonus" :key="uuid(bonus)">
      <span class="badge" :class="[sy.count >= bonus.count ? 'badge-danger' : '']">{{ bonus.count }}</span>
      {{ bonus.value }}
    </div>
    <div class="">
      <div class="btn px-0 text-white">Champions:</div>
      <img
        :src="getAvatarURL(c.name, 2)"
        :alt="c.name"
        class="ml-1 champ-icon"
        width="30px"
        height="30px"
        v-for="c in origins[name]"
        :key="uuid(c)"
        :class="checkTeam(c.name) ? 'in-team' : 'not-in-team'"
      />
      <!-- <ChampIcon v-for="c in origins[name]" :key="uuid(c)" :champ="c" /> -->
    </div>
  </div>
  <div class="text-white text-left" v-else-if="type == 'class'" style="max-width: 300px">
    <div class>
      <img :src="getIconURL(name)" :alt="name" width="30px" />
      <h6 class="mx-2 d-inline">{{name}}</h6>
    </div>
    <div class="">{{ classObject[name].effect }}</div>
    <div class="border-dark border-top border-bottom px-2" v-for="bonus in classObject[name].bonus" :key="uuid(bonus)">
      <span class="badge" :class="[sy.count >= bonus.count ? 'badge-danger' : '']">{{ bonus.count }}</span>
      {{ bonus.value }}
    </div>
    <div class="">
      <div class="btn px-0 text-white">Champions:</div>
      <img
        :src="getAvatarURL(c.name, 2)"
        :alt="c.name"
        :id="'not-' + c.name.split(' ')[0]"
        class="ml-1"
        width="30px"
        height="30px"
        v-for="c in classes[name]"
        :key="uuid(c)"
        :class="checkTeam(c.name) ? 'in-team' : 'not-in-team'"
      />
      <!-- <ChampIcon v-for="c in classes[name]" :key="uuid(c)" :champ="c" /> -->
    </div>
  </div>
</template>

<script>
import ChampIcon from "./ChampIcon";

export default {
  name: "TraitInfo",
  components: { ChampIcon },
  props: {
    name: String,
    type: String,
    set: {
      type: Number,
      default: 2
    },
    sy: Object,
    team: Array
  },
  methods: {
    checkTeam: function(name) {
      let a = false;
      this.team.forEach(t => {
        if (t.name == name) {
          a = true;
        }
      });
      return a;
    }
  }
};
</script>

<style>
.not-in-team {
  filter: grayscale(1);
}
.in-team {
  border: 1px solid orange;
}
</style>